<style>
/*Eduction and EXPERIENCE*/
.back_white {
    padding: 45px;
}
.single-detail-item {
    display: block;
    margin-bottom: 30px;
    background-color: #fff;
    position: relative;
    padding: 30px;
    padding-bottom: 20px;
    padding-right: 40px;
}

a.list_skill {
    position: relative;
    background-color: #fff;
    display: block;
    color: #333;
    font-weight: 500;
    font-size: 15px;
    text-align: center;
    text-transform: uppercase;
    line-height: 1;
    padding: 40px 10px;
    border-radius: 0px;
    margin-bottom: 30px;
    -webkit-transition: all .3s ease-in;
    transition: all .3s ease-in;
    cursor: pointer;
    text-decoration: none;
}

.single-detail-item .year {
    margin-bottom: 20px;
    display: inline-block;
    padding: 10px 20px;
    color: #00203f;
    background-color: #adefd1;
    font-weight: 500;
    font-size: 17px;
    line-height: 1;
    border-radius: 3px;
    -webkit-transition: all .3s ease-in;
    transition: all .3s ease-in;
}

a.list_skill:hover,
.list_skill.active,
.year:hover {
    background-color: #adefd1;
    color: #00203f;
    text-decoration: none;
}

a.list_skill:hover:before,
.list_skill.active:before {
    visibility: visible;
    opacity: 1;
}

a.list_skill:before {
    position: absolute;
    left: 100%;
    content: "";
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-left: 10px solid #adefd1;
    border-bottom: 10px solid transparent;
    visibility: hidden;
    opacity: 0;
    margin-left: -1px;
}
.tab-pane.fade {
    display: none;
}
.tab-pane.fade.active {
    display: block;
}


/*skill*/
.skill-name{
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    margin: 20px 0;
    }
.skill-per{
    height: 20px;
    background-color: #adefd1;
    width: 0;
    transition: 1s linear;
    position: relative;
}
  
.bar::before{
    content: attr(per);
    position: absolute;
    padding: 4px 6px;
    background-color: #000;
    color: #fff;
    font-size: 12px;
    border-radius: 4px;
    top: -35px;
    right: 0;
    transform: translateX(50%);
}
  
.bar::after{
    content: '';
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: #000;
    top: -16px;
    right: 0;
    transform: translateX(50%) rotate(45deg);
    border-radius: 2px;
}

.chart {
  background-color: #DADADA;
  height: 15px;
  position: relative;
}

.chart .bar {
  height: 100%;
  -moz-animation: animate-bar 1.25s 1 linear;
  -webkit-animation: animate-bar 1.25s 1 linear;
  -ms-animation: animate-bar 1.25s 1 linear;
  -o-animation: animate-bar 1.25s 1 linear;
  animation: animate-bar 1.25s 1 linear;
}
</style>
