
/*Smooth scroll section*/
window.onload = function() {
    document.querySelectorAll('.nav_menu li a').forEach(anchor => {
        anchor.addEventListener('click', function (e) {
            e.preventDefault();
            let current_href = this.getAttribute('href');
            let current_val = document.getElementById(current_href);
            if(current_href == 'portfolio'){
                window.location.href = "/kn/portfolio";
            }
            let offsetTop = current_val.offsetTop
            let header_size = document.getElementsByTagName('header')[0].offsetHeight;
            window.scrollTo({left: current_val.offsetLeft ,top: offsetTop - header_size,behavior: 'smooth'});
        });
    })
    let exit_menu = document.getElementsByClassName("toggle_menu").length;
    if(exit_menu == 1){
        document.getElementsByClassName("toggle_menu")[0].onclick = function () {
            const addclass=  document.getElementsByClassName("menusbar")[0];
            const existclass =  document.querySelectorAll(".menusbar.d-block").length;
            const menu_icon =  document.getElementsByClassName("humber")[0]
            if(existclass == 0){
                addclass.classList.add("d-block");
                menu_icon.classList.add("icon-remove");
                menu_icon.classList.remove("icon-reorder");
            }
            else{
                addclass.classList.remove("d-block");
                menu_icon.classList.remove("icon-remove");
                menu_icon.classList.add("icon-reorder");
            }
        }
    }
};


/*Sticky header*/

    const onScroll = () => {
    const navbar = document.getElementsByTagName('header')[0];
    const scroll = document.documentElement.scrollTop
    const cureent_postion = document.documentElement.clientWidth;
    if(cureent_postion > 768){
        if (scroll > 300) {
            navbar.classList.add("stick_header");
        } else {
            navbar.classList.remove("stick_header")
        }
    }
    
    if (scroll > 2200) {
        document.querySelectorAll('.skills .chart .bar ').forEach(anchor => {
         let newe = anchor.getAttribute('per'); 
         anchor.style.width= newe+'%';
        })
    }   
    }
    window.addEventListener('scroll', onScroll)


 


