<template>
<div class="container">
    <div class="row">
        <div class="col-lg-12 my-5">
            <h2 class="text-center heading">Portfolio</h2>
        </div>

        <div class="col-lg-12">
            <ul class="unique_cate p-0 text-center m-auto">
                <li>
                    <a cure_slug="all" :class="active_menu" @click="active_filter">Show All</a>
                </li>
                <li v-for="(catrgors,index) in catrgors" :key="index">
                    <a :cure_slug="catrgors.slug" :class="activeclass == catrgors.slug ? 'active':'' " @click="active_filter">{{catrgors.name}}</a>
                </li>
            </ul>
        </div>

        <div v-for="(catrgors,index) in listservice" :key="index" :class="['col-lg-4 grid_item', activeclass == 'all' ?'d-show':''|| activeclass == catrgors.slug ?'d-show':'d-none']" :cat_name="catrgors.slug">

            <div class="psingle_item py-4">
                <a href="javascript:void(0);" @click="proejct_detail(catrgors.id)" data-toggle="modal" data-target="#exampleModalLong">
                    <img :src="catrgors.img" class="img-fluid">
                    <span class="read_more"><i class="icon-eye-open" aria-hidden="true"></i></span>
                </a>
            </div>
        </div>
    </div>
</div>

<!-- Modal -->
<div class="modal fade" id="exampleModalLong" tabindex="-1" role="dialog" aria-labelledby="exampleModalLongTitle" aria-hidden="true">
    <div class="modal-dialog" role="document">
        <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLongTitle">{{project_title}}</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                <div class="row">
                    <div class="col-12">
                        <img :src="project_imag" class="w-100">
                    </div>
                    <div class="col-12 mt-4 text-left">
                        <p><i class="icon-file-alt"></i> <label><strong>Project : </strong></label> {{project_type}}</p>
                        <p><i class="icon-time"></i> <label><strong>Duration : </strong></label> {{project_time}}</p>
                        <p><i class="icon-code"></i> <label><strong>Technologies : </strong></label> {{project_techn}}</p>
                    </div>
                </div>
            </div>
            <div class="modal-footer">
                <a :href="project_url" target="_blank" class="text-left mr-auto btn review"><i class="icon-external-link"></i> Preview</a>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import axios from 'axios'
//var passwordHash = require('password-hash');

export default {
    name: 'Portfolio',
    data() {
        return {
            activeclass: '',
            active_menu: '',
            listservice: [],
            project_title: '',
            project_type: '',
            project_time: '',
            project_techn: '',
            project_url: ''
        }
    },
    computed: {
        catrgors() {
            return this.listservice.filter((v, i, a) => a.findIndex(t => (t.name === v.name)) === i);
        }
    },
    created() {
        this.active_menu = 'active'
        this.getList();
        return this.activeclass = 'all';
    },
    methods: {
        active_filter(event) {
            const value = event.currentTarget.getAttribute('cure_slug');
            this.active_menu = value == 'all' ? 'active' : '';
            return this.activeclass = value;
        },
        proejct_detail(id) {
            let find_data = this.listservice.find(curr_data => curr_data.id == id);
            this.project_imag = find_data.img;
            this.project_type = find_data.Project;
            this.project_time = find_data.duration;
            this.project_techn = find_data.technologies;
            this.project_url = find_data.url;
            return this.project_title = find_data.title;
        },
        async getList() {
            
            await axios.post('/jdata/prod/v1/gprotfoli',{
                'key':'$2y$10$GNl1Oe.YMIQ25ng87VbgMOgFcRGQLq/D6TN1gUpguajuX/iY0Wosy'
            }).then(
                response => this.listservice = response.data.listservice
            ).catch(
                error => {
                    this.listservice = error.message;
            });
        }
    }
};
</script>

<style scoped>
.psingle_item a {
    position: relative;
    display: block;
}

.psingle_item a:hover:before {
    opacity: .3;
    visibility: visible;
    height: 100%;
}

.psingle_item a::before {
    position: absolute;
    width: 100%;
    height: 0%;
    text-align: center;
    background: #0bc0ba;
    left: 0;
    bottom: 0;
    content: "";
    opacity: 0;
    visibility: hidden;
    -webkit-transition: .3s;
    transition: .3s;
}

.psingle_item a:hover .read_more {
    visibility: visible;
}

.read_more {
    color: #0bc0ba;
    opacity: 1;
    visibility: visible;
    position: absolute;
    left: 43%;
    bottom: 43%;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #ffffff;
    display: inline-block;
    line-height: 40px;
    font-size: 20px;
    text-align: center;
    -webkit-transition: .35s;
    transition: .3s;
    opacity: 1;
    visibility: hidden;
    -webkit-box-shadow: 0 14px 28px rgb(0 0 0 / 25%), 0 10px 10px rgb(0 0 0 / 22%);
    box-shadow: 0 14px 28px rgb(0 0 0 / 25%), 0 10px 10px rgb(0 0 0 / 22%);
    z-index: 1;
}

.review {
    background: #0bc0ba;
    color: #fff;
}

ul.unique_cate li {
    display: inline-block;
    padding: 10px 19px;
}
.unique_cate li a:hover{
    color: #adefd1; 
}
.unique_cate li a {
    text-decoration: none;
    cursor: pointer;
}

.unique_cate li .active {
    background: #00203f;
    color: #adefd1;
    text-decoration: none;
    padding: 13px;
    border-radius: 5px;
}
</style>
