<template>
<div class="row">
    <div class="col-lg-12 my-5">
        <h2 class="text-center heading">Client Testimonial</h2>
    </div>
    <div class="col-lg-12">
        <Carousel :itemsToShow="show_slider" :autoplay="2500" :wrapAround="true">
            <Slide v-for="testimonails in testimonail" :key="testimonails">
                <div class="carousel__item p-4 mx-2">
                    <img class="img_testi" :src="testimonails.img" />
                    <h5 class="customer_name mt-3">{{testimonails.shortname}}</h5>
                    <p class="desci_client mt-3">{{testimonails.review}}</p>
                </div>
            </Slide>
            <template #addons>
                <Pagination />
            </template>
        </Carousel>
    </div>
</div>
</template>

<script>
import 'vue3-carousel/dist/carousel.css';

import {
    Carousel,
    Pagination,
    Slide
} from 'vue3-carousel';

export default {
    name: 'ClientReview',
    data() {
        return {
            testimonail: [{
                    'id': 1,
                    'shortname': 'Tashinga M',
                    'img': require('@/assets/images/clients/c1.jpg'),
                    'review': 'It was great working with Kaushik, he was very responsive and got the job done quickly. will definitely work with him again!'

                },
                {
                    'id': 2,
                    'shortname': 'Rick D',
                    'img': require('@/assets/images/clients/c2.png'),
                    'review': 'Perfect experience. A quick little job Kaushik took care of swiftly!'

                },
                {
                    'id': 3,
                    'shortname': 'Roxsanne D',
                    'img': require('@/assets/images/clients/c3.jpg'),
                    'review': 'Fantastic to work with!'
                },
                {
                    'id': 4,
                    'shortname': 'Jules P',
                    'img': require('@/assets/images/clients/c4.jpg'),
                    'review': 'Kaushik is very skilled, super efficient and is a pleasure to work with'
                },
                {
                    'id': 5,
                    'shortname': ' Mamadou D',
                    'img': require('@/assets/images/clients/c5.png'),
                    'review': 'Kaushik was punctual with scheduled meet ups. He went above and beyond resolving the payement issue we had. Will work with him again and strongly recommend you use his services.Cheers!'
                }

            ],
        }
    },
    components: {
        Carousel,
        Slide,
        Pagination,
    },
    computed:{
        show_slider(){
            const cureent_postion = document.documentElement.clientWidth;
          
            let slider ='';
            if(cureent_postion < 600){
                return '1';
            }
            if(cureent_postion < 769){
                return '2';
            }
            if(cureent_postion > 768){
                return '3';
            }
            return slider;
        }
       
    }
};
</script>

<style>
.carousel__item {
    min-height: 200px;
    width: 100%;
    background-color: #adefd1;
    color: var(--carousel-color-white);
    font-size: 20px;
    border-radius: 8px;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 8px 2px #eeee;
    border: 1px solid #00203f;
}

.carousel__slide {
    padding: 22px;
}

.customer_name {
    color: #00203f;
    font-family: 'Poppins_Regular';
}

.desci_client {
    color: #00203f;
    font-size: 14px;
}

.carousel__prev,
.carousel__next {
    box-sizing: content-box;
    border: 5px solid white;
}

.carousel__slide>.carousel__item {
    transform: scale(1);
    opacity: 0.5;
    transition: 0.5s;
    height: 15em;
}

.carousel__slide--visible>.carousel__item {
    opacity: 1;
    transform: rotateY(0);
}

.carousel__slide--active {
    opacity: 1;
}

.carousel__slide--next>.carousel__item {
    transform: scale(0.9) translate(-10px);
}

.carousel__slide--prev>.carousel__item {
    transform: scale(0.9) translate(10px);
}

.carousel__slide--active>.carousel__item {
    transform: scale(1.1);
}

.img_testi {
    width: 4em !important;
    border-radius: 2em;
    background-size: cover;
}

.carousel__pagination-button--active {
    background-color: #00203f !important;
}

.carousel__pagination .carousel__pagination-button {
    background-color: #001f3f86;
}
</style>
